<template>
	<article class="header full_page">
		<div class="block_content bg">
			<div class="logo">
				<img src="@/assets/images/main-logo.png" alt="logo">
			</div>
			<div class="action_button">
				<a :href="buttonLink" class="base_link" target="_blank">{{buttonText}}</a>
			</div>
		</div>
	</article>
</template>
<style lang="scss">
.header{
	.bg{
		background: url('../../assets/images/header-image.png') no-repeat 50% 50% / cover;
	}
}
</style>
<script>
export default {
	name: 'blockHeader',
	props: ['buttonText', 'buttonLink'],
};
</script>



