<template>
	<article class="contacts half_page">
		<div class="block_content">
			<div class="contact_row">
				<div class="number">
					<a :href="`tel:${fullnumber}`" class="base_link">
						<span class="region">{{region}}</span>
						{{number}}
					</a>
				</div>
			</div>
		</div>
	</article>
</template>
<style lang="scss">
.contacts{
	.block_content{
		.number{
			color: white;
			.base_link{
				display: flex;
				align-items: start;
				line-height: 1;
			}
			font-size: 52px;
			.region{
				line-height: 35px;
				font-size: 24px;
				margin-right: 10px;
			}
		}
	}
}
@media screen {
	@media (max-width: 530px){
		.half_page{
			height: 50vh;
		}
		.contacts .block_content{
			justify-content: center;
		}
	}
}

</style>
<script>
export default {
	name: 'contacts',
	props: ['fullnumber'],
	data() {
		return {
			region: '',
			number: '',
		};
	},
	methods: {
		extractRegion() {
			const countryCode = this.fullnumber.slice(0, 2);
			const operator = this.fullnumber.slice(2, 5);
			const firstEntry = this.fullnumber.slice(5, 8);
			const secondEntry = this.fullnumber.slice(8, 10);
			const lastEntry = this.fullnumber.slice(10, 12);
			this.region = `${countryCode} ${operator} `;
			this.number = `${firstEntry} ${secondEntry} ${lastEntry}`;
		},
	},
	created() {
		this.extractRegion();
	},
};
</script>



