<template>
	<article class="video_block full_page">
		<div class="block_content bg">
			<div class="page_title"></div>
			<div class="action_button">
				<a :href="buttonLink" class="base_link">{{buttonText}}</a>
			</div>
		</div>
	</article>
</template>
<style lang="scss">
.video_block{
	.bg{
		background: url('../../assets/images/video-bg.png') no-repeat 50% 50% / cover;
	}
}
</style>
<script>
export default {
	name: 'blockVideo',
	props: ['buttonText', 'buttonLink'],
};
</script>



