			buttonLink="#"
			buttonText="Скачать презентацию"/>
		<blockTour
			buttonLink="/tour"
			buttonText="Виртуальный тур"/>
		<blockPlan />
		<blockVideo buttonText="смотреть видео"
			buttonLink="#"/>
		<contacts fullnumber="+79789077770"/>
	</main>
</template>
<script>
import blockHeader from './blocks/header';
import blockTour from './blocks/tour';
import blockPlan from './blocks/plan';
import blockVideo from './blocks/video';
import contacts from './blocks/contacts';

export default {
	name: 'Index',
	components: { blockHeader, blockTour, blockPlan, blockVideo, contacts },
};
</script>



