import Vue from 'vue';
import Router from 'vue-router';
import Index from '@/components/Index';
import Tour from '@/components/Tour';

Vue.use(Router);

export default new Router({
	mode: 'history',
	routes: [
		{
			path: '/',
			name: 'Главная',
			component: Index,
		},
		{
			path: '/tour',
			name: 'Виртуальный тур',
			component: Tour,
		},
	],
});



