<template>
	<article class="virtual_tour full_page">
		<div class="block_content bg">
			<div class="page_title">Виртуальный тур</div>
			<div class="action_button">
				<a :href="buttonLink" class="base_link">{{buttonText}}</a>
			</div>
		</div>
	</article>
</template>
<style lang="scss">
.virtual_tour{
	.bg{
		background: url('../../assets/images/tour-image.png') no-repeat 50% 50% / cover;
	}
}
</style>
<script>
export default {
	name: 'blockTour',
	props: ['buttonLink', 'buttonText'],
};
</script>



