<template>
	<article class="block_plan full_page">
		<div class="block_content bg">
			<div class="buttons_row">
				<div class="buttons_column">
					<div class="action_button">
						<a href="#" class="base_link download_icon" id="villa31">villa 3.1</a>
					</div>
					<div class="action_button">
						<a href="#" class="base_link download_icon" id="villa32">villa 3.2</a>
					</div>
				</div>
				<div class="buttons_column">
					<div class="action_button">
						<a href="#" class="base_link download_icon villa41">villa 4.1</a>
					</div>
					<div class="action_button">
						<a href="#" class="base_link download_icon villa41">villa 4.2</a>
					</div>
				</div>
			</div>
			<div class="plan_scheme">
				<div class="plan_scheme__image"></div>
				<div class="village_object" id="village32">
					<div class="village_number">3.2</div>
				</div>
				<div class="village_object" id="village31">
					<div class="village_number">3.1</div>
				</div>
				<div class="village_object" id="village42">
					<div class="village_number">4.2</div>
				</div>
				<div class="village_object" id="village41">
					<div class="village_number">4.1</div>
				</div>
				<div class="village_object" id="village22">
					<div class="village_number">2.2</div>
				</div>
				<div class="village_object" id="village21">
					<div class="village_number">2.1</div>
				</div>
				<div class="village_object" id="village12">
					<div class="village_number">1.2</div>
				</div>
				<div class="village_object" id="village11">
					<div class="village_number">1.1</div>
				</div>
			</div>
			<div class="buttons_row">
				<div class="buttons_column">
					<div class="action_button">
						<a href="#" class="base_link download_icon villa21">villa 2.1</a>
					</div>
					<div class="action_button">
						<a href="#" class="base_link download_icon villa22">villa 2.2</a>
					</div>
				</div>
				<div class="buttons_column">
					<div class="action_button">
						<a href="#" class="base_link download_icon villa11">villa 1.1</a>
					</div>
					<div class="action_button">
						<a href="#" class="base_link download_icon villa12">villa 1.2</a>
					</div>
				</div>
			</div>
		</div>
	</article>
</template>
<style>
#villa32:hover #village32{
	border-color: #6f8f95 !important;
}
</style>
<style lang="scss">
.block_plan{
	background: #576770;
	.bg{
		background: url('../../assets/images/plan-bg.png') no-repeat 50% 50% / cover;
	}
	.block_content{
		padding: 117px;
		position: relative;
		.buttons_row{
			width: 100%;
			display: flex;
			justify-content: space-between;
			.buttons_column{
				.action_button{
					margin-bottom: 20px;
					a {
						font-weight: 400;
					}
				}
				.action_button:last-child{
					margin-bottom: 0
				}
			}
		}
		.plan_scheme{
			width: 1048px;
			height: 100%;
			top: 0;
			position: absolute;
			&__image{
				pointer-events: none;
				position: absolute;
				width: 100%;
				height: 100%;
				mix-blend-mode: screen;
				background: url('../../assets/images/plan-scheme.png') no-repeat 50% 50% / contain;
			}
			.village_object{
				position: absolute;
				color: white;
				font-weight: 400;
				border-radius: 50%;
				border: 2px solid;
				border-color: white;
				width: 34px;
				height: 34px;
				display: flex;
				align-items: center;
				justify-content: center;
				cursor: pointer;
			}
			#village32{ left: 43%; top: 26%; }
			#village31{ top: 26%; right: 47%; }
			#village41{ top: 40%; right: 14%; }
			#village42{ top: 40%; right: 22%; }
			#village22{ bottom: 47%; left: 21%; }
			#village21{ bottom: 42%; left: 27%; }
			#village11{ bottom: 27%; right: 33%; }
			#village12{ bottom: 27%; right: 39%; }
		}
	}
}
@media screen {
	@media (max-width: 1065px){
		.block_plan .block_content{
			padding: 117px 57px;
			.plan_scheme{
				width: 768px;
				#village32{ left: 42%; top: 32%; }
				#village31{ top: 32%; right: 46%; }
				#village41{ top: 42%; right: 14%; }
				#village42{ top: 42%; right: 21%; }
				#village21{ bottom: 46%; left: 20%; }
				#village22{ bottom: 43%; left: 26%; }
				#village11{ bottom: 33%; right: 32%; }
				#village12{ bottom: 33%; right: 38%; }
			}
		}
	}
	@media (max-width: 785px){
		.block_plan .block_content {
			.plan_scheme{
				width: 512px;
				.village_object{ transform: scale(.8); }
				#village32{ left: 40%; top: 35%; }
				#village31{ right: 45%; top: 35%; }
				#village42{ top: 45%; right: 20%;}
				#village41{ top: 45%; right: 12%;}
				#village22{ bottom: 44%; left: 25%; }
				#village21{ bottom: 46%; left: 19%;}
				#village12{ bottom: 36.5%; right: 37.5%;}
				#village11{ bottom: 36.5%; right: 30%;}
			}
		}
	}
	@media (max-width: 530px){
		.block_plan .block_content {
			padding: 117px 29px;
			.plan_scheme{
				width: 380px;
				.village_object{ transform: scale(0.6) }
				#village32{ top: 40%; left: 39%; }
				#village31{ right: 44%; top: 40%;}
				#village42{ right: 19%; }
				#village41{ right: 11%;}
				#village22{ bottom: 46%; left: 23.5%; }
				#village21{ bottom: 47%; left: 18%; }
				#village12{ bottom: 40.5%; right: 37%; }
				#village11{ bottom: 40.5%; right: 30%;}
			}
		}
	}
	@media (max-width: 410px) {
		.block_plan .block_content{
			.buttons_row{
				.buttons_column{
					.action_button{
						transform: scale(0.8);
					}
				}
			}
		}
	}
}
</style>
<script>
export default {
	name: 'blockPlan',
};
</script>



