<template>
	<main class="tour">
		<iframe src="https://files.360crimea.ru/boofer/other/CasaCascata/"
			frameborder="0" allowfullscreen
			class="tour_view"></iframe>
		<div class="action_button">
			<a href="/" class="base_link tour_home" ref="buttonHome">Вернуться на главную</a>
		</div>
	</main>
</template>
<script>
export default {
	name: 'Tour',
	methods: {
		changeOpacity() {
			const newOpacity = 0.5;
			setTimeout(() => {
				this.$refs.buttonHome.style = `opacity: ${newOpacity}`;
			}, 10000);
		},
	},
	created() {
		this.changeOpacity();
	},
};
</script>
<style lang="scss" scoped>
.tour{
	margin: 0;
	padding: 0;
	position: relative;
	&_view{
		z-index: 0;
		height: 100vh;
		width: 100%;
		margin-bottom: -5px;
	}
	.action_button{
		z-index: 1;
		position: absolute;
		top: 20px;
		right: 45px;
	}
	&_home{
		color: white;
		text-shadow: 1px 1px 10px rgba(0, 0, 0, .5);
		box-shadow: 1px 1px 10px 1px rgba(0, 0, 0, .5);
		border-radius: 20px;
		border: 1px solid white;
		backdrop-filter: blur(5px);
		background: rgba(255, 255, 255, .1);
		transition: opacity .3s ease;
		&:hover{
			opacity: 1 !important;
		}
	}
}
@media screen {
	@media (max-width: 740px){
		.tour{
			.action_button{
				width: 100%;
				right: 0;
				top: calc(100% - 90px);
			}
		}
	}
}
</style>



